<template>
  <v-app>
    <v-dialog v-model="visible" data-testid="app-dialog">
      <component :is="component" data-testid="app-dialog-content" />
      <!-- <div data-testid="app-dialog-content">
        this should be only in dom when modal has popped
      </div> -->
      <!-- <v-btn text @click="hideModal"> Close </v-btn> -->
      <!-- <v-btn text @click="hideModal"> Save </v-btn> -->
      <!-- <v-btn text @click.self="hideModal"> Save </v-btn> -->
    </v-dialog>

    <!-- <v-app-bar hide-on-scroll app v-if="isAuthenticated"> -->
    <v-app-bar hide-on-scroll app>
      <v-app-bar-nav-icon
        @click.stop="toggleNavBar = !toggleNavBar"
        data-testid="hamburger-menu-toggle"
      ></v-app-bar-nav-icon>
      <v-toolbar-title
        class="text-capitalize"
        data-testid="app-bar-context-title"
      >
        {{ title }}
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn
        v-if="!isAuthenticated"
        link
        :to="headerMap.login.route"
        icon
        data-testid="login-button"
      >
        <v-icon>{{ headerMap.login.icon }}</v-icon>
      </v-btn>
      <v-btn
        v-if="!isAuthenticated"
        link
        :to="headerMap.register.route"
        icon
        data-testid="register-button"
      >
        <v-icon>{{ headerMap.register.icon }}</v-icon>
      </v-btn>
      <v-btn
        v-if="isAuthenticated"
        :to="headerMap.avatar.route"
        icon
        data-testid="avatar-button"
      >
        <v-icon>{{ headerMap.avatar.icon }}</v-icon>
      </v-btn>
      <v-btn
        v-if="isAuthenticated"
        @click.stop="handleLogout"
        icon
        data-testid="logout-button"
      >
        <v-icon>{{ headerMap.logout.icon }}</v-icon>
      </v-btn>
    </v-app-bar>

    <v-navigation-drawer
      v-model="toggleNavBar"
      fixed
      temporary
      data-testid="nav-drawer"
    >
      <v-list dense nav>
        <v-list-item
          v-for="link in navList"
          :key="link.title"
          link
          :to="link.route"
        >
          <v-list-item-icon>
            <v-icon>{{ link.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title class="text-capitalize">
              {{ link.title }}
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-main>
      <router-view />
    </v-main>
  </v-app>
</template>

<script>
import { mapState, mapMutations, mapGetters, mapActions } from "vuex";
// import { mapState, mapMutations } from "vuex";
import Vue from "vue";

export default {
  components: {},
  name: "App",

  data: () => ({
    // isAuthenticated: false,
    toggleNavBar: false,
    component: "",
  }),
  computed: {
    ...mapState({
      // visible: `modalVisible`,
      modalComponent: `modalComponent`,
    }),
    ...mapState(["icons"]),
    ...mapGetters("authentication", ["isAuthenticated"]),
    // ...mapGetters(["authentication/isAuthenticated"]),
    // ...mapGetters({ isAuthenticated: "authentication/isAuthenticated" }),
    visible: {
      ...mapState({ get: "modalVisible" }),
      ...mapMutations({ set: "HIDE_MODAL" }),
    },
    title() {
      return this.$route.name || "Authorize";
    },
    headerMap() {
      return {
        register: {
          title: "register",
          route: "/register",
          icon: this.icons.register.icon,
        },
        login: {
          title: "login",
          route: "/login",
          icon: this.icons.login.icon,
        },
        logout: {
          title: "logout",
          // TODO: this is not a route yet
          route: "/logout",
          icon: this.icons.logout.icon,
        },
        // TODO: populate this on change in isAuthenticated
        avatar: {
          title: "user",
          route: "/user",
          // TODO: make this default to an icon mapping to a role, then user avatar if available
          icon: this.icons.user.icon,
        },
      };
    },
    navList() {
      return [
        {
          title: "home",
          route: "/",
          icon: this.icons.home.icon,
        },
        {
          title: "about",
          route: "/about",
          icon: this.icons.help.icon,
        },
        {
          title: "user",
          route: "/user",
          icon: this.icons.user.icon,
        },
        {
          title: "moderator",
          route: "/moderator",
          icon: this.icons.moderator.icon,
        },
        {
          title: "admin",
          route: "/admin",
          icon: this.icons.admin.icon,
        },
        {
          title: "settings",
          route: "/settings",
          icon: this.icons.settings.icon,
        },
      ];
    },
  },
  methods: {
    ...mapMutations({
      hideModal: `HIDE_MODAL`,
    }),
    ...mapActions("authentication", ["logout"]),
    ...mapActions("alert", ["clear"]),
    handleLogout() {
      this.logout();
      this.$router.push("/").catch(() => {
        /* catch router:Error:NavigationDuplicated when user logs out from '/' */
      });
    },
  },
  watch: {
    $route() {
      // clear alert on location change
      // this.$store.dispatch("alert/clear");
      this.clear();
      // TODO: NOT WORKING - try to hide nav bar on from.login|register to a route that would pop modal
      // this.toggleNavBar = false;
    },
    visible(val) {
      console.log(`modal visibility changed: ${val}`);
      if (val) {
        this.toggleNavBar = false;
      }
    },
    modalComponent(componentName) {
      if (!componentName) return;
      Vue.component(componentName, () =>
        import(`@/components/modal/${componentName}`)
      );
      this.component = componentName;
    },
  },
};
</script>