// export const alert = {
export default {
    namespaced: true,
    state: {
        // type: null,
        // message: null
        type: '',
        message: ''
    },
    actions: {
        success({ commit }, message) {
            commit('success', message);
        },
        error({ commit }, message) {
            commit('error', message);
        },
        clear({ commit }) {
            commit('clear');
        }
    },
    mutations: {
        success(state, message) {
            state.type = 'alert-success';
            state.message = message;
        },
        error(state, message) {
            state.type = 'alert-danger';
            state.message = message;
        },
        clear(state) {
            // state.type = null;
            // state.message = null;
            state.type = '';
            state.message = '';
        }
    }
}
