export default {
  state: () => ({
    // namespaced: true,
    showMetadata: false,
    sortOrder: false, // false = abc; true = 123
    selectedKeys: [],
  }),
  mutations: {
    SET_SORT_ORDER: (state, val) => {
      state.sortOrder = val
    },
    SET_SHOW_METADATA: (state, val) => {
      state.showMetadata = val
    },
    STORE_SEARCH_KEYS: (state, keyList) => {
      state.selectedKeys = keyList
    },
    RESET_PREFS: (state) => {
      state.showMetadata = false;
      state.sortOrder = false;
      state.selectedKeys = [];
    },
  },
  actions: {
    setSortOrder: ({ commit }, val) => {
      console.log(`store:setSortOrder`)
      commit('SET_SORT_ORDER', val)
    },
    setShowMetadata: ({ commit }, val) => {
      console.log(`store:setShowMetadata`)
      commit('SET_SHOW_METADATA', val)
    },
    storeSearchKeys: ({ commit }, keyList) => {
      console.log(`store:storeSearchKeys`)
      commit('STORE_SEARCH_KEYS', keyList)
    },
    resetPrefs: ({ commit }) => {
      console.log(`store:resetPrefs`)
      commit('RESET_PREFS')
    }
  },
  getters: {
    getPrefs: (state) => {
      return {
        sortOrder: state.sortOrder,
        showMetadata: state.showMetadata,
        searchKeys: state.selectedKeys
      }
    }
  }
};